import Vue from 'vue';
import VueI18n from 'vue-i18n';
import en from './lang-en.json';
import zh from './lang-zh.json';
import $ from "jquery";

Vue.use(VueI18n);
const lan = window.language;
const messages = {};
messages['lang-zh'] = zh;
messages['lang-en'] = en;
export const i18n = new VueI18n({
    locale: lan,
    messages,
});

$(document).on("switch-lang",function () {
    const lang = window.localStorage.getItem('lang');
    i18n.locale = lang;
});